import { IconButton } from '@chakra-ui/button';
import React from 'react';
import { FaInstagram } from 'react-icons/fa';

const Instagram = () => {
  return (
    <IconButton
      aria-label=''
      ml={{ base: 1, md: 2 }}
      icon={<FaInstagram />}
      isRound={true}
      alignSelf={'center'}
      onClick={() => window.open('https://www.instagram.com/abhiram_hari_/')}
    ></IconButton>
  );
};

export default Instagram;
